import { fireDb } from '@/plugins/firebase'
import Vue from 'vue'
import Vuex from 'vuex'
import { Store } from "vuex";

Vue.use(Vuex)

const toSave = ['users', 'items', 'borrows']

export const state = () => ({
    users: null,
    items: null,
    borrows: null,
})

export const mutations = {
    set_object(state, data) {
        state[data.key] = data.value
    },
    insert_item(state, data) {
        if (state[data.collection]) {
            state[data.collection][data.id] = data.value
        } else {
            let result = {}
            result[data.id] = data.value
            state[data.collection] = result
        }
    },
    update_item(state, data) {
        if (state[data.collection]) {
            state[data.collection][data.id] = data.value
        }
    },
    delete_item(state, data) {
        if (state[data.collection]) {
            delete state[data.collection][data.id]
        }
    },
}

export const actions = {
    async get_collection({ commit }, name) {
        let result = {}
        try {
            let querySnapshot = await fireDb.collection(name).get()
            if (querySnapshot.empty) return {}
            querySnapshot.forEach(
                doc => result[doc.id] = doc.data()
            )
            if (toSave.includes(name)) {
                commit('set_object', { key: name, value: result })
            }
            console.log("name", name, result)
        } catch (e) {
            console.log("Error", e)
        }
        return result
    },
    async update_item({ commit }, data) {
        try {
            await fireDb.collection(data.collection).doc(data.id).update(data.value)
            commit('update_item', data)
        } catch (e) {
            console.log("Error", e)
        }
    },
    async insert_item({ commit }, data) {
        try {
            console.log("II", data.collection, data.value)
            let result = await fireDb.collection(data.collection).add(data.value)
            console.log("RES", result)
            commit('insert_item', {
                collection: data.collection,
                id: result.id,
                value: data.value,
            })
            return result.id
        } catch (e) {
            console.log("Error", e)
        }
    },
    async delete_item({ commit }, data) {
        try {
            console.log("II", data.collection, data.value)
            await fireDb.collection(data.collection).doc(data.id).delete()
            commit('delete_item', {
                collection: data.collection,
                id: data.id,
            })
        } catch (e) {
            console.log("Error", e)
        }
    },
}

const store = new Store({
    state,
    // getters,
    mutations,
    actions
});

export default store;
